<template>
	<div class="Cashdesk">
		<div class="title"><img src="../imgs/left.png" alt="" />收银台</div>
		<div class="dis">
			<div>
				<div class="zhibox"><img class="zhi" src="../assets/w_pay.png" alt="" /></div>
				<span class="zhi-title">微信支付</span> 
			</div>
			<div><input type="radio" name="zhifu" /></div>
			
		</div>
		<div class="dis">
			<div>
				<div class="zhibox"><img class="zhi" src="../assets/alipay.png"/> </div>
				<span class="zhi-title">支付宝支付</span>
			</div>
			<div><input type="radio" name="zhifu" /></div>
			
		</div>
		<div class="dis">
			<div>
				
					<div class="zhibox"><img class="zhi" src="../assets/meitonghui.png" alt="" /></div>
					
				<span class="zhi-title">美币支付</span>
				
			</div>
			<div><input type="radio" name="zhifu" id="" value="" /></div>
		</div>
		<div class="fukuanbox">
			<div>
				<div>支付: <span class="money">12.00</span> </div>
				<div>剩余00:23:56 请尽快付款</div>
			</div>
			<div class="fuk-outer">
				<button class="fuk">付款</button>
			</div>
			
		</div>
	</div>
</template>

<script>
	export default {
		name: 'Cashdesk',
		data() {
			return {}
		},

		methods: {}
	}
</script>

<style>
	@import "../css/base.css";
	body {
		background-color: #f1f1f1;
	}
	
	.title {
		position: relative;
		height: 45px;
		background-color: #fff;
		line-height: 45px;
		font-size: 15px;
	}
	
	.title img {
		position: absolute;
		width: 10px;
		height: 15px;
		left: 20px;
		top: 15px;
	}
	.Cashdesk .dis{
		margin-top: 1px;
		display: flex;
		padding: 20px;
		background: #fff;
		justify-content: space-between;
	}
	.Cashdesk .zhi{
		/*width: 30px;*/
		height: 30px;
	}
	.Cashdesk .zhibox{
		width: 50px;
		display: inline-block;
	}
	.Cashdesk .zhi-title{
		display: inline-block;
	}
	.Cashdesk .money::before{
		content: "¥";
	}
	.Cashdesk .fukuanbox{
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
		padding: 10px;
		background: #fff;
		display: flex;
		text-align: left;
		
		justify-content: space-between;
	}
	.Cashdesk .fuk-outer{
		position: relative;
	}
	.Cashdesk .fuk{
		position: absolute;
		top: 50%;
		right: 10px;
		padding: 10px;
		color: #fff;
		width: 80px;
		-webkit-transform: translateY(-50%);
		background: #fb1578;
	}
</style>


