<template>

  <div>
    <div class="noAddress">
      <div class="title"><img src="../imgs/left.png" alt=""/>管理收货地址</div>
      <div class="imgbox">
        <img src="../imgs/local.png" alt=""/>
        <p>还没有添加收货地址哦</p>
      </div>
      <button>添加</button>
    </div>
    <!-- 已经添加了地址-->
    <div class="alreadyAdd">
      <div class="title"><img src="../imgs/left.png" alt=""/>管理收货地址</div>
      <div class="address">
        <div class="adressDetail"><span>黄小姐</span><span>12349852100</span>

          <div>广州市天河区888888888</div>
        </div>
        <div class="edit"><input type="checkbox"/><span class="default">默认地址</span>

          <div class="editAd"><img src="../imgs/delete.png" alt=""/><span>删除</span></div>
          <div class="editAd"><img src="../imgs/edit.png" alt=""/><span>编辑</span></div>

        </div>
      </div>
      <div class="addNewAdress">
        <button>添加新地址</button>
      </div>
    </div>
  </div>


</template>

<script type="text/ecmascript-6">
  export default{
    name: 'address',
    data (){
      return {}
    },

    methods: {}
  }
</script>

<style>
  @import "../css/base.css";

  body {
    background-color: #f1f1f1;
  }

  .alreadyAdd {
    display: none;
  }

  .title {
    position: relative;
    height: 45px;
    background-color: #fff;
    line-height: 45px;
    font-size: 15px;
    margin-bottom: 11px;
  }

  .title img {
    position: absolute;
    width: 10px;
    height: 15px;
    left: 20px;
    top: 15px;
  }

  .address {
    width: 100%;
    background-color: #fff;
    height: 90px;
  }

  .address .adressDetail {
    padding: 0 20px;
    overflow: hidden;
    width: 100%;
    text-align: left;
    height: 56px;
    border-bottom: 1px solid #ededed;
  }

  .address .adressDetail span {
    margin-top: 8px;
    box-sizing: border-box;
    float: left;
    display: block;
    width: 50%;
    overflow: hidden;
    margin-bottom: 6px;
  }

  .address .adressDetail span:last-of-type {
    text-align: right;
  }

  .address .adressDetail div {
    overflow: hidden;
    width: 100%;
    height: 20px;
  }

  .address .edit {
    padding: 0 20px;
    text-align: left;
    line-height: 34px;
  }

  .address .edit .default {
    color: #f94190;
  }

  .address .edit input {
    width: 16px;
    height: 16px;
    border: 1px solid #F94190;
    margin-right: 4px;
  }

  .editAd {
    float: right;
    width: 49px;
    height: 100%;
  }

  .editAd:last-of-type {
    margin-right: 10px;
  }

  .editAd img {
    margin-top: -3px;
    width: 15px;
    height: 15px;
    margin-right: 4px;
  }

  /*添加新地址*/
  .addNewAdress {
    position: fixed;
    bottom: 26px;
    left: 0;
    width: 100%;
    padding: 0 20px;
  }

  .addNewAdress button {
    display: block;
    width: 100%;
    line-height: 35px;
    color: #fff;
    background-color: #fb1678;
    height: 35px;
    border-radius: 6px;
  }
  /*===================未添加*/
.imgbox{
  margin-top:160px ;
  margin-bottom: 30px;
}
  .imgbox img{
    width: 58px;
    height: 80px;
    margin-bottom: 18px;
  }
  .imgbox p{
    color: #8f8f8f;
  }
  button{
    display: block;
    height: 36px;width: 100px;
    color: #fff;
    border-radius: 6px;
    background-color: #fb1678;margin: 0 auto;
  }

</style>



