<template>
	<div class="shopcart">
		<div class="top">
			<span>购物车</span>
			<span class="guanli">管理</span>
		</div>
		<div class="top_one">
			<img class="pho" src="../assets/store.png" />
			<span>自营商店</span>
			<img class="left" src="../assets/left_1.png" />
		</div>
		<ul class="goodlist">
			<li class="list" v-for="checkb in checkboxData">
				<div class="list-left">
					<input type="checkbox" v-model='checkboxModel' :value='checkb.id' />
					<img class="head" src="../assets/pic_03.png" />
				</div>
				<div class="list-right">
					<div>公司是看得见疯狂世界的看法建设大街</div>
					<div>90ml</div>
					<div class="money">55</div>
					<div class="controlcount">
						<span class="remove">-</span>
						<span>1</span>
						<span class="add">+</span>
					</div>
				</div>
			</li>
		</ul>

		

		<div class="bot">
			<div class="botquan"><input name="all" type="checkbox" v-model='checked' @click="checkedAll" /><span>全选</span> </div>
			<div>不含运费 <span>合计:</span><span class="money">55</span>
				<button class="jiesuan">结算(1)</button>
			</div>
		</div>
		<app-footer></app-footer>
	</div>
</template>

<script>
	import Tabbar from '@/components/Tabbar'
	export default {
		name: 'Shopcart',

		compoents: {
			Tabbar
		},
		data() {
			return {
				goodsList: [{

						name: '1',
						price: '19.00'
					},
					{

						name: '2',
						price: '19.00'
					},
					{

						name: '3',
						price: '19.00'
					},
					{

						name: '4',
						price: '19.00'
					},
					{

						name: '5',
						price: '19.00'
					},
				],
				selectArr: [],
				checkboxData: [{
					id: '1',
					value: '苹果'
				}, {
					id: '2',
					value: '荔枝'
				}, {
					id: '3',
					value: '香蕉'
				}, {
					id: '4',
					value: '火龙果'
				}],
				checkboxModel: ['1', '3', '4'],
				checked: false

			}
		},
		computed: {

		},
		methods: {
			checkedAll: function() {
				var _this = this;
				console.log(_this.checkboxModel);
				if(this.checked) { //实现反选
					_this.checkboxModel = [];
				} else { //实现全选
					_this.checkboxModel = [];
					_this.checkboxData.forEach(function(item) {
						_this.checkboxModel.push(item.id);
					});
				}
			}

		},
		watch: { //深度 watcher
			'checkboxModel': {
				handler: function(val, oldVal) {
					if(this.checkboxModel.length === this.checkboxData.length) {
						this.checked = true;
					} else {
						this.checked = false;
					}
				},
				deep: true
			}
		},
		created() {

		}
	}
</script>

<style>
	@import "../css/base.css";
	.shopcart {
		background: #e5e5e5;
		height: 100%;
	}
	
	.shopcart .top {
		background: #fb1578;
		color: #fff;
		padding: 15px 0;
		text-align: center;
		overflow: hidden;
		position: relative;
	}
	
	.shopcart .guanli {
		position: absolute;
		right: 20px;
		top: 50%;
		-webkit-transform: translateY(-50%);
	}
	
	.shopcart .top_one {
		display: flex;
		padding: 10px;
		background: #fff;
		position: relative;
		border: 1px solid #f4f4f4;
	}
	
	.shopcart .pho {
		height: 20px;
		margin-right: 10px;
	}
	
	.shopcart .left {
		position: absolute;
		right: 20px;
		height: 20px;
		-webkit-transform: rotate(180deg);
	}
	
	.shopcart .bot {
		position: fixed;
		bottom: 50px;
		/*padding: 10px;*/
		left: 0;
		width: 100%;
		background: #fff;
		display: flex;
		height: 40px;
		line-height: 40px;
		justify-content: space-between;
	}
	
	.shopcart .botquan {
		margin-left: 10px;
	}
	
	.shopcart .money::before {
		content: '¥';
	}
	
	.shopcart .jiesuan {
		display: inline-block;
		width: 60px;
		height: 100%;
		text-align: center;
		background: #fc4190;
	}
	
	.shopcart .list {
		padding: 10px;
		box-sizing: border-box;
		display: flex;
		background: #fff;
	}
	
	.shopcart .list-left {
		/*float: left;*/
		flex: 3;
		margin-right: 10px;
	}
	
	.shopcart .head {
		width: 80px;
		height: 80px;
	}
	
	.shopcart .list-right {
		flex: 7;
		text-align: left;
		position: relative;
	}
	
	.shopcart .controlcount {
		position: absolute;
		right: 0;
		bottom: 0;
	}
	
	.shopcart .remove {
		background: #f5f5f5;
	}
	
	.shopcart .add {
		background: #c9c9c9;
	}
	
	.shopcart .controlcount span {
		display: inline-block;
		width: 20px;
		text-align: center;
	}
	
	.shopcart .goodlist {
		margin-bottom: 100px;
	}
</style>


