<template>

  <div>
    <div class="title">入会注册</div>
    <form action="">
      <div>
        <label for="name">姓名</label>
        <input type="text" id="name" placeholder="请输入你的名字"/>
      </div>
      <div>
        <label for="phone">手机号</label>
        <input type="tel"  maxlength="11" id="phone" placeholder="请输入你的手机号"/>
      </div>
      <div>
        <label for="wechatNum">微信号</label>
        <input type="text"  id="wechatNum" placeholder="请输入你的微信号"/>
      </div>
      <div>
        <label >出生日期</label>
        <edit-time :datelist='datelist' :options='options'></edit-time>
      </div>
      <div>
        <label>所在城市</label>
        <input type="text"  />
      </div>

    </form>
    <div><button>同意注册</button></div>
  </div>


</template>

<script type="text/ecmascript-6">
  export default{
    name: 'Login',
    data (){
      return {}
    },

    methods: {}
  }

</script>

<style>
  @import "../css/base.css";

  body {
    background-color: #f1f1f1;
  }

  .title {
    color: #fff;
    position: relative;
    height: 45px;
    background-color: #fb1678;
    line-height: 45px;
    font-size: 15px;
  }

  .title img {
    position: absolute;
    width: 10px;
    height: 15px;
    left: 20px;
    top: 15px;
  }

  form {
    padding: 11px;
    width: 100%;
    text-align: left;
  }

  form div {
    padding-left: 6px;
    width: 100%;
    border-bottom: 1px solid #8f8f8f;
    height: 38px;
    line-height: 38px;
  }

  form div label {
    display: block;
    width: 68px;
    float: left;
  }

  form div input {
    background-color: #f1f1f1 !important;
    width: 72%;

  }
  button{
    width: 162px;
    height: 32px;
    background-color: #fb1678;
    line-height: 32px;
    color: #fff;
    border-radius: 5px;
    margin: 28px auto 0;  }


</style>



