<template>
  <div>
    <van-nav-bar
  title="美妆任领"
  left-text=""
  left-arrow
  @click-left="$router.push('/home')"
  fixed/>
    <div class="title">美妆任领</div>
<van-tabs 
:sticky="sticky"
v-model="active" 
@click="tabClick">
  <van-tab v-for="index in 4" :key="index" :title="tabText[index-1]">
  </van-tab>
</van-tabs>
    <!-- <div class="titleDirect">
      <span>综合</span>
      <span>新品</span>
      <span>销量</span>
      <span>价格</span>
    </div> -->
    <div class="product">
      <ul>
        <van-list
        v-model="loading"
        :finished="finished"
        :offset="offset"
        @load="onLoad(active, page)"
        >
        <li v-if="list" v-for="item in list" @click="$router.push(`pd/${item.goods_id}`)">
          <div class="pic"><img :src="item.original_img"  v-lazy="item.original_img" /></div>
          <div class="ProName">{{ item.goods_name}}</div>
          <div class="vipPrice">会员价￥200</div>
          <div>
            <span class="maketPrice">{{ item.shop_price}}</span>
            <span class="freight">{{ item.sales_moon}}</span>
          </div>
        </li>
        </van-list>
      </ul>
    </div>
  </div>


</template>

<script type="text/ecmascript-6">
export default {
  name: "list",
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      page: 0,
      limit: 20,
      ic: false,
      offset: 400,
      active: 0,
      sticky: true,
      tabText: ["综合", "新品", "销量", "价格"]
    };
  },

  mounted() {},

  methods: {
    tabClick(index) {
      this.list = [];
      this.finished = false;
      this.onLoad(index, (this.page = 0));
    },
    onClickLeft() {},
    onLoad(type = 0, page = 0) {
      let that = this;
      // that.loading = false; //加载完成
      // that.finished = true;

      //  that.loading = true;
      this.$axios
        .get(
          `http://193.112.201.63/api/goods/getVipGoodsList/type/${type}/page/${page}/limit/20`
        )
        .then(function(response) {
          if (response.data.status == 2) {
            that.finished = true;
          }
          that.list.push(...response.data.data);

          // that.finished = false;
              //that.loading = false;
          console.log(that.loading);
          console.log(that.finished);
          that.page = that.page + 1;
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  }
};
</script>

<style>
@import "../css/base.css";

body {
  background-color: #f1f1f1;
}

.title {
  position: relative;
  height: 45px;
  background-color: #fff;
  line-height: 45px;
  font-size: 15px;
}

.title img {
  position: absolute;
  width: 10px;
  height: 15px;
  left: 20px;
  top: 15px;
}

.titleDirect {
  text-align: center;
  height: 34px;
  background-color: #fff;
  width: 100%;
  display: flex;
  line-height: 34px;
}

.titleDirect span {
  flex: 1;
  color: #8f8f8f;
}

/*product*/
.product {
  width: 100%;
  height: 100%;
}

.product ul {
  width: 100%;
  height: 500px;
}
.product li div:last-of-type {
  padding: 2px 7px;
}
.product li div:last-of-type span:last-of-type {
  float: right;
}
.product li {
  background-color: #fff;
  width: 49%;
  /* height: 240px; */
  float: left;
  margin: 8px 1% 0;
}
.product li:nth-of-type(2n + 1) {
  margin-left: 0;
}

.product li:nth-of-type(2n) {
  margin-right: 0;
}
.product .pic {
  width: 100%;
  /* height: 160px; */
}
.product .pic img {
  width: 100%;
  height: 100%;
}
.product ul li .ProName,
.vipPrice {
  text-align: left;
  padding: 4px 7px;
  overflow: hidden;
  text-overflow: ellipsis;
  height: 26px;
  white-space: nowrap;
}
.vipPrice {
  color: #fb0100;
}
.maketPrice {
  float: left;
  color: #989898;
}
</style>



