  <div class="content">

    <h1>登录</h1>

    <div class="wechatWay">
    <img src="../imgs/wechat.png" alt=""/>
      <span>微信登录</span>
    </div>
    <button>忘记密码</button>
    <div class="line"></div>
    <div class="otherway">
      <div><img src="../imgs/qq.png">

        <p>QQ登录</p></div>
      <div><img src="../imgs/qq.png">

        <p>免密登录</p></div>
      <div><img src="../imgs/account.png">

        <p>账号登录</p></div>
    </div>
