<style>
  @import "../css/base.css";
  p {
    margin: 10px 0;
    font-size: 14px;
  }

  .content {
    width: 100%;
    height: 100%;
  }

  h1 {
    padding-top: 100px;
    font-weight: normal;
    font-size: 20px;
  }

  button {
    background-color: #fff;
    color: #8f8f8f;
  }

  .wechatWay {
    position: relative;
    color: #fff;
    height: 36px;
    width: 80%;
    background-color: #09bb06;
    border-radius: 20px;
    line-height: 36px;
    margin: 40px auto 25px;
  }

  .wechatWay img {
    position: relative;
    width: 26px;
    height: 25px;
    margin-top: -3px;

  }

  .line {
    width: 70px;
    border: 1px solid #ededed;
    margin: 175px auto 69px;
  }

  .otherway {
    margin: 0 20px;
    display: flex;
    text-align: center;

  }

  .otherway div {
    flex: 1
  }

  .otherway div img {
    width: 32px;
    height: 32px;
  }

  .register {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    border-top: 1px solid #ededed;
    text-align: center;
    color: #fc167b;
    line-height: 44px;
  }

</style>



