import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import OrderQuery from '@/components/OrderQuery'
import loginManage from '@/components/loginManage'
import adressManage from '@/components/adressManage'
import Login from '@/components/Login'
import MakeupFree from '@/components/makeupFree'
import vipCard from '@/components/vipCard'
import productDetails from '@/components/productDetails'
import joinRegister from '@/components/joinRegister'
import drawCheck from '@/components/drawCheck'
import ourProducts from '@/components/ourProducts'
import confirmOrder from '@/components/confirmOrder'
import Upgrade from '@/components/Upgrade'
import contactService from '@/components/contactService'
import Shopcart from '@/components/Shopcart'
import Cashdesk from '@/components/Cashdesk'
import Home from '@/components/Home'
import Set from '@/components/Set'
import Tabbar from '@/components/Tabbar'

Vue.component('app-footer', Tabbar)
Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/pd/:id',
            name: 'pd',
            component: productDetails
        },
        {
            path: '/test',
            name: 'test',
            component: Set
        },
        {
            path: '/login',
            name: '',
            component: Login,
        },
        {
            path: '/list',
            name: 'list',
            component: MakeupFree
        },
        {
            path: '/register',
            name: '',
            component: joinRegister,
        },
        {
            path: '/',
            name: '',
            component: Home,
        },
        {
            path: '/contact',
            name: '',
            component: contactService,
        },
        {
            path: '/op',
            name: '',
            component: ourProducts,
        },
        {
            path: '/confirmOrder',
            name: '',
            component: confirmOrder,
        },
        {
            path: '/Shopcart',
            name: 'Shopcart',
            component: Shopcart
        },
    ]
})



