<template>
	<div class="Home">
		<div class="top">
			<div class="to"><img class="im" src="../assets/logo.png"/></div>
			<div class="tw">
				<img src="../assets/serch.png" alt="" />
				<input type="text" placeholder="搜索美妆" />
				
			</div>
			<div class="tt"><img class=" ri" src="../assets/list.png"/></div>
		</div>
		<div>
			<img class="banner" src="../assets/banner.png"/>
			
		</div>
		<div class="xiaoxi">
			<img  class="news" src="../assets/news.png"/>
			上岛咖啡快速贷款放款速度快福克斯的方式打开了
		</div>
		<div class="dis">
			<div class="list" @click="$router.push({ path: 'list', query: { is: 'vip' }})">
				<div>美妆任领</div>
				<div>sadas</div>
				<div>领取</div>
				<img class="imgw" src="../assets/lipstick.png"/>
			</div>
			<div class="list">
				<div>美妆任领</div>
				<div>sadas</div>
				<div>领取</div>
				<img class="imgw" src="../assets/lipstick.png"/>
			</div>
		</div>
		<div>
			<img class="banner" src="../assets/ad.png"/>
		</div>
		<div class="type">
			<span>综合</span>
			<span>销量</span>
			<span>价格</span>
			<span>券额</span>
		</div>
	
		<div class="conte">
       
                <!-- <p>{{ item.goods_id}}</p> -->
                <div class="conte-li" v-if="list" v-for="item in list" :key="item.id">
                    <div class="li-img"><img :src="item.original_img" v-lazy="item.original_img" /></div>
                    <div>{{ item.goods_name}}</div>
                    <div class="li-con">
                        <div class="money">{{ item.shop_price}}</div>
                        <div>月销{{ item.sales_moon}}笔</div>
                    </div>
             
            </div>
			<!-- <div class="conte-li">
				<div class="li-img"><img src="../assets/pic_03.png"/></div>
				<div>
				资深堂
				</div>
				<div class="li-con">
					<div class="money">38.00</div>
					<div>月销1笔</div>
				</div>
			</div> -->
		</div>	
	</div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      list: null
    };
  },
  mounted() {
    let that = this;
    console.log(this);
    //http://193.112.201.63/api/goods/getGoodsList
    // http://193.112.201.63/api/goods/getVipGoodsList
    this.$axios
      .get("http://193.112.201.63/api/goods/getGoodsList")
      .then(function(response) {
        that.list = response.data;
        console.log(response);
      })
      .catch(function(error) {
        console.log(error);
      });
  },

  methods: {}
};
</script>

<style>
@import "../css/base.css";
.Home .top {
  background: #fb1578;
  display: flex;
  padding: 10px 2px;
}
.Home .top img {
  height: 20px;
}
.Home .to {
  flex: 1;
  position: relative;
}

.Home .tw {
  flex: 8;
  background: #fff;
  padding: 5px;
  border-radius: 20px;
  text-align: left;
  padding-left: 20px;
}

.Home .tt {
  flex: 1;
  position: relative;
}
.Home .im {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
}
.Home .ri {
  right: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
}
.Home .banner {
  width: 100%;
}
.Home .xiaoxi {
  color: #fb1377;
  background: #e5e5e5;
  padding: 5px;
  width: 95%;
  margin: 20px auto;
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  border-radius: 20px;
}
.Home .news {
  width: 10px;
  display: inline-block;
  vertical-align: middle;
  margin-top: -2px;
}
.Home .dis {
  display: flex;
  justify-content: space-around;
}
.Home .type {
  display: flex;
  justify-content: space-around;
  padding: 10px 0;
}
.Home .list {
  background: #f8f8f8;
  flex: 1;
  margin: 2%;
}
.Home .imgw {
  width: 30px;
  height: 60px;
}
.Home .conte {
  padding: 10px 0;
  overflow: hidden;
  background: #f5f5f5;
}
.Home .conte-li {
  padding: 10px 5px;
  float: left;
  width: 48%;
  text-align: left;
  background: #fff;
  margin: 0 1%;
}
.Home .li-img {
  text-align: center;
  margin-bottom: 10px;
}
.Home .li-con {
  display: flex;
  justify-content: space-between;
}
.Home .money::before {
  content: "¥";
}
</style>


