<template>
	<div class="tabbarbox">
		<div class="outer" >
			<div @click="tab(0)">
				<div v-if="num==0"><img src="../assets/tabbar/home_page_active.png"/></div>
				<div v-else> <img src="../assets/tabbar/home_page.png"/></div>
				<div :class="num==0?'green':''">首页</div>
			</div>
			<div @click="tab(1)">
				<div v-if="num==1"><img src="../assets/tabbar/initiation_active.png"/></div>
				<div v-else> <img src="../assets/tabbar/initiation.png"/></div>
				<div :class="num==1?'green':''">入会</div>
			</div>
			<div @click="tab(2)">
				<div v-if="num==2"><img src="../assets/tabbar/shopping_active.png"/></div>
				<div v-else><img src="../assets/tabbar/shopping.png"/></div>
				<div :class="num==2?'green':''">购物车</div>
			</div>
			<div @click="tab(3)">
				<div v-if="num==3"><img src="../assets/tabbar/mine_active.png"/></div>
				<div v-else><img src="../assets/tabbar/mine.png"/></div>
				<div :class="num==3?'green':''">我的</div>
			</div>
		
		</div>
	</div>
</template>

<script>
	export default{
		name:'Tabbar',
		
		data(){
			return{
//				data:[{
//					"title":"首页",
//					"img":"../assets/tabbar/home_page.png",
//					"select":'../assets/tabbar/home_page_active.png'
//				},{
//					"title":"入会",
//					"img":"../assets/tabbar/initiation.png",
//					"select":'../assets/tabbar/initiation_active.png'
//				},{
//					"title":"购物车",
//					"img":"../assets/tabbar/shopping.png",
//					"select":'../assets/tabbar/shopping_active.png'
//				},{
//					"title":"我的",
//					"img":"../assets/tabbar/mine.png",
//					"select":'../assets/tabbar/mine_active.png'
//				}],
				select:false,
				num:0
			}
		},
		methods:{
			
		tab(index){
			this.num=index
			console.log(this.num)
			switch (index){
				case 2:
				this.$router.push({
					name: 'Shopcart'
				})
					break;
				default:
					break;
			}
		}
		
		},
		created(){
			
		}
	}
</script>

<style>
	.tabbarbox{
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
		padding-top:10px ;
		font-size: 14px;
		background: #fff;
	}
	.tabbarbox .outer{
		display: flex;
		justify-content: space-around;
	}
	.tabbarbox img{
		height: 20px;
	}
	.tabbarbox .green{
		color: #fc1678;
	}
</style>


